import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import { Row, Col } from 'antd';
import cx from 'classnames';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';

export const View8 = () => {
  return (
    <DIV className="paymentPackages">
      <section className="pt-6 pb-0 mb-6 min-lg:pb-3 min-lg:px-6 min-lg:border min-h-200px min-lg:border-border rounded-xl">
        <h2 className="mb-3 text-xl max-sm:text-base min-lg:text-main">
          {ctx.apply('i18n.t', 'Payment.StepOneTitle')}
        </h2>
        <ResponsiveProps
          sm={{ rowJustify: 'space-between', rowGutter: 16, spanCol: 12 }}
          md={{ rowGutter: 24, spanCol: 8 }}
          lg={{ rowGutter: 24, spanCol: 6 }}
        >
          {(resProps) => (
            <Row
              jusify={_.get(resProps, 'rowJustify', 'start')}
              gutter={[_.get(resProps, 'rowGutter', 16), _.get(resProps, 'rowGutter', 16)]}
              wrap={true}
            >
              {ctx.get('advisor.packages').map((item) => (
                <Col key={item.id} className="min-w-40 gutter-row" span={_.get(resProps, 'spanCol', 6)}>
                  <article
                    key={item.id}
                    onClick={() => {
                      ctx.apply('form.setFieldValue', 'serviceId', _.get(item, 'service_id'));
                    }}
                    className={cx(
                      'w-full relative border-2 flex items-center justify-center w-40 cursor-pointer rounded-xl h-100px flex-col',
                      { 'border-brandb bg-white500': _.get(item, 'service_id') === ctx.get('form.values.serviceId') },
                      { 'bg-background2': _.get(item, 'service_id') !== ctx.get('form.values.serviceId') }
                    )}
                  >
                    {!!_.get(item, 'discount.percentageText') && (
                      <Row
                        align="middle"
                        justify="center"
                        className="absolute top-0 right-0 h-5 px-3 min-w-10 bg-branda rounded-tr-xl rounded-bl-xl text-xxs text-white500 max-w-22 text-ellipsis-1"
                      >
                        {_.get(item, 'discount.percentageText')}
                      </Row>
                    )}
                    {!!_.get(item, 'discount.amountText') && (
                      <Row
                        align="middle"
                        justify="center"
                        className="absolute top-0 right-0 h-5 px-3 bg-branda rounded-tr-xl rounded-bl-xl text-xxs text-white500 max-w-22 text-ellipsis-1"
                      >
                        -{_.get(item, 'discount.amountText')}
                      </Row>
                    )}
                    <label className="mb-1">{item.packageName}</label>
                    <p className="mb-0 text-brandb">{item.priceTxt}</p>
                  </article>
                </Col>
              ))}
            </Row>
          )}
        </ResponsiveProps>

        {!!ctx.get('form.errors.serviceId') && (
          <div className="mt-1 text-xs text-red500">{ctx.get('form.errors.serviceId')}</div>
        )}
      </section>
    </DIV>
  );
};
export default View8;
